.root {
  background: white;
  width: 100%;
  max-width: 1500px;
  margin: auto auto 20px;
  padding: 20px 0px;
}

.userLoadTitle {
  font-size: 24px;
  font-weight: 600;
  margin: 5px 20px;
  display: flex;
  justify-content: space-between;
}

.userLoadCardsContainer {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin: 20px 20px;
}

.userLoadCard {
  flex: 1 1 calc(25% - 20px); /* Adjust the percentage as needed */
  box-sizing: border-box; 
}

.loadingCont {
  margin: 50px 0px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}