.root {
  padding: 15px;
}

.cardHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.menuItem {
  padding: 0px;
  min-width: 150px;
}

.dateContainer {
  display: flex;
  flex-direction: row;
  border-radius: 10px;

  .date {
    padding: 5px;
    padding-left: 10px;
    width: 0px;
    flex: 1 1 0px;
  }
}

.datePlaceholderCont {
  padding: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.eventStart {
  display: flex;
  align-items: center;
}

.commHeader {
  display: flex;
}

.alertStatus {
  border-radius: 5px;
  padding: 5px;
  min-width: 100px;
}
