.filterCont {
  padding: 20px;
  padding-bottom: 0px;
  display: flex;
  justify-content: space-between;

  @media (max-width : 600px) {
    flex-direction: column;
  }
}

.loadingCont {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.disclaimerCont {
  margin: 20px;
  padding: 10px;
}

.eventsCont {
  min-height: 300px;
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}